import Axios from '@/plugins/axios'
import FirebaseMixin from '@/mixins/FirebaseMixin'
import NavigationMixin from '@/mixins/NavigationMixin'

const BarberMixin = {
  mixins: [FirebaseMixin, NavigationMixin],
  computed: {
    barber() {
      return this.$store.getters['BarberStore/barber']
    },
    barberState() {
      return this.$store.getters['BarberStore/barber']
    }
  },
  methods: {
    clearBarberAndBarbershopSession () {
      this.$store.dispatch('BarberStore/logout')
      this.$store.dispatch('BarbershopStore/logout')
      delete localStorage.routePathAdmin
      this.goTo('barber-auth')
    },
    getBarber() {
      return this.$store.getters['BarberStore/barber']
    },
    getBarberByBarbershopIdOnService(barbershopId) {
      Axios.get(`/barbershop/${barbershopId}/barber?status=ativo`)
        .then(response => {
          if (response.data.codigo === 200) {
            this.onGetBarberByBarbershopIdOnServiceSuccess(response.data.corpo)
          } else this.onGetBarberByBarbershopIdOnServiceError(response.data.corpo)
        })
        .catch((err) => {
          this.onGetBarberByBarbershopIdOnServiceError('No foi possvel recuperar os dados da barbearia!')
          window.console.log(err)
        })
    },
    onGetBarberByBarbershopIdOnServiceError(err) { window.console.log(err) },
    onGetBarberByBarbershopIdOnServiceSuccess(response) { window.console.log(JSON.stringify(response)) },
    getToken() {
      return this.$store.getters['BarberStore/payloadToken']
    },
    isAutenticated() {
      return this.$store.getters['BarberStore/tokenValido']
    },
    logout() {
      const barberDeviceToken = localStorage.barberDeviceToken || null
      if (barberDeviceToken) {
        this.removeDeviceToken(barberDeviceToken).finally(() => {
          delete localStorage.barberDeviceToken
          this.clearBarberAndBarbershopSession()
        })
      } else this.clearBarberAndBarbershopSession()
    },
    removeDeviceToken (barberDeviceToken) {
      return this.$axios.post(`${this.$url_api}/barber/remove-device-token`, {
        deviceToken: barberDeviceToken
      })
    },
    storeBarber(token) {
      this.$store.dispatch('UserStore/logout')
      this.$store.dispatch('BarberStore/autenticar', token)
    }
  }
}

export default BarberMixin